import { hasClass, addClass, removeClass, toggleClass, remove, getArrayItemById } from './library';
import newNoteItem from './newNoteItem';

export default function noteListInteraction() {
  noteListMenuDom.addEventListener('click', (e) => {
    const target = e.target;
    const activeNoteListItem = d.querySelector('.noteListItem.active');
    const id = target.parentNode.id;
    // Edit
    if (hasClass(target, 'noteListItem-text')) {
      readNoteData(id);
      removeClass(activeNoteListItem, 'active');
      toggleClass(target.parentNode, 'active');
      draftDom.focus();
    }
    // New
    if (hasClass(target, 'newNote')) {
      const createdNotesCount = Number(localStorage.createdNotesCount) + 1;
      newNoteItem(createdNotesCount);
    }
    // Delete
    if (hasClass(target, 'noteListItem-deleteIcon')) {
      deleteNote(id);
      const parentNode = target.parentNode;
      addClass(parentNode, 'shrink');
      // setTime for the shrink animation
      setTimeout(() => {
        const isActive = hasClass(parentNode, 'active');
        remove(parentNode);
        // Activate first note list item
        const firstNoteListItem = d.querySelector('.noteListItem');
        if (isActive && firstNoteListItem) {
          addClass(firstNoteListItem, 'active');
          const noteList = JSON.parse(localStorage.noteList);
          draftDom.value = getArrayItemById(noteList, firstNoteListItem.id).text;
        }
        if (!firstNoteListItem) {
          draftDom.value = '';
          draftDom.focus();
        }
      }, 300);
    }
  });
  function readNoteData(id) {
    const noteListData = JSON.parse(localStorage.noteList);
    for (let i = noteListData.length - 1; i >= 0; i -= 1) {
      if (noteListData[i].id === Number(id)) {
        draftDom.value = noteListData[i].text;
        break;
      }
    }
  }
  function deleteNote(id) {
    const noteListData = JSON.parse(localStorage.noteList);
    for (let i = noteListData.length - 1; i >= 0; i -= 1) {
      if (noteListData[i].id === Number(id)) {
        noteListData.splice(i, 1);
        localStorage.noteList = JSON.stringify(noteListData);
      }
    }
  }
}
