export function fontFamily(value) {
  localStorage.setItem('fontFamily', value);
  ga('send', 'event', 'customize', 'fontFamily', value);
}

export function fontSize(value) {
  localStorage.setItem('fontSize', value);
  ga('send', 'event', 'customize', 'fontSize', value);
}

export function fontColor(value) {
  localStorage.setItem('fontColor', value);
  ga('send', 'event', 'customize', 'fontColor', value);
}

export function fontColorPad(value) {
  localStorage.setItem('fontColorPad', value);
}

export function backgroundColor(value) {
  localStorage.setItem('backgroundColor', value);
  ga('send', 'event', 'customize', 'backgroundColor', value);
}
export function backgroundColorPad(value) {
  localStorage.setItem('backgroundColorPad', value);
}
