export default function initLog() {
  function setRated() {
    if (Draft.rated !== 'true') {
      if (Draft.rated !== null) {
        if (Number(Draft.draftCount) > 40) {
          d.querySelector('.wrapperFeedback').style.display = 'block';
        }
      } else {
        localStorage.rated = false;
      }
    }
  }

  function setLog() {
    if (Draft.log === null) {
      d.querySelector('.wrapperLog').style.display = 'block';
    }
  }
  setRated();
  setLog();
}
