import { hasClass, addClass, removeClass, toggleClass, isColor, indexInSameClassName } from './library';
import saveFile from './saveFile';
import * as inputData from './inputData';
import * as render from './renderCustomizeChange';
import * as share from './share';
import { showPopover } from './popover';
import getFontFamilyList from './fontFamilyList';
import initCustomize from './initCustomize';

export default function menuInteraction() {
  const menu = d.getElementById('menu');
  // Menu Item Interaction
  const menuItemInteraction = (target) => {
    const parentNode = target.parentNode;
    if (hasClass(target, 'menuItem-title')) {
      ga('send', 'event', 'menuItem', 'click', target.innerText);
    } if (hasClass(target, 'menuItem-title') && hasClass(parentNode, 'groupItem')) {
      const activeMenuItem = d.querySelector('.menuItem.active');
      if (activeMenuItem) {
        removeClass(d.querySelector('.menuItem.active'), 'active');
      }
      toggleClass(parentNode, 'active');
    } else if (hasClass(target, 'menuItem-list-title') && hasClass(parentNode, 'groupItem')) {
      if (customizeLoaded === false && target.innerText === 'Font & Background') {
        getFontFamilyList();
        initCustomize();
        customizeLoaded = true;
      }
      const activeMenuListItem = d.querySelector('.menuItem-list.active');
      if (activeMenuListItem) {
        removeClass(activeMenuListItem, 'active');
      }
      toggleClass(parentNode, 'active');
    }
  };

  const fontFamilyInteraction = (target) => {
    if (hasClass(target, 'selector-fontFamily')) {
      const targetValue = target.value;

      // Render Data
      render.fontFamily(targetValue);
      // Input Data
      inputData.fontFamily(targetValue);
    }
  };

  // Font Size Range
  const rangeInputInteraction = (target) => {
    if (hasClass(target, 'fontSize-rangeChanger')) {
      const targetValue = target.value;

      // Render Data
      render.fontSize(targetValue);

      // Input Data
      inputData.fontSize(targetValue);
    }
  };

  // Color Pad
  const removeClosestActiveColorPad = (target) => {
    const activeItem = target.closest('.sublist-item').getElementsByClassName('colorPadItem active')[0];
    removeClass(activeItem, 'active');
  };

  const chooseColorInteraction = (target) => {
    if (hasClass(target, 'colorPadItem') && !hasClass(target, 'active')) {
      removeClosestActiveColorPad(target);
      addClass(target, 'active');

      // Render Data
      const targetValue = getComputedStyle(target).getPropertyValue('background-color');
      const sublistTitle = target.closest('.sublist-item').getElementsByClassName('sublist-title')[0];
      const number = indexInSameClassName(target, 'colorPadItem', sublistTitle.parentNode);
      const name = sublistTitle.getAttribute('name');
      render[name](target, targetValue);

      // Input Data
      inputData[name](targetValue);
      inputData[`${name}Pad`](number);
    }
  };

  const detectColorPad = (target, className) => {
    const targetValue = target.value;
    if (className === 'colorPadInput' && isColor(targetValue)) {
      const colorPadItem = target.parentNode.getElementsByClassName('colorPadItem')[0];
      colorPadItem.style.backgroundColor = targetValue;
      removeClosestActiveColorPad(target);
      addClass(colorPadItem, 'active');

      // Render Data
      const sublistTitle = target.closest('.sublist-item').getElementsByClassName('sublist-title')[0];
      const name = sublistTitle.getAttribute('name');
      const number = sublistTitle.parentNode.getElementsByClassName('colorPadItem').length - 1;
      render[name](target, targetValue);

      // Input Data
      inputData[name](targetValue);
      inputData[`${name}Pad`](number);
    }
  };

  const triggerColorPicker = (target) => {
    if (hasClass(target, 'colorPadItem-customize')) {
      target.nextElementSibling.nextElementSibling.click();
    }
  };
  const colorPickerInteraction = (target) => {
    if (hasClass(target, 'colorPicker')) {
      // Render Data
      const targetValue = target.value;
      const sublistTitle = target.closest('.sublist-item').getElementsByClassName('sublist-title')[0];
      const name = sublistTitle.getAttribute('name');
      const number = sublistTitle.parentNode.getElementsByClassName('colorPadItem').length - 1;
      render[name](target, targetValue, 'colorPicker');

      // Input Data
      inputData[name](targetValue);
      inputData[`${name}Pad`](number);
    }
  };


  // Save Draft
  const saveDraft = (target) => {
    if (hasClass(target, 'saveDraftItem')) {
      saveFile();
    }
  };

  const saveDraftShortcut = () => {
    d.onkeydown = (e) => {
      if ((e.metaKey || e.ctrlKey) && e.keyCode === 83) {
        e.preventDefault();
        d.querySelector('.saveDraftItem').click();
      }
    };
  };

  saveDraftShortcut();

  // Share
  const shareTo = (target) => {
    if (hasClass(target, 'shareItem')) {
      const id = target.id;
      share[id]();
      ga('send', 'event', 'share', 'click', id);
    }
  };
  const moreShare = (argument) => {
    const target = argument;
    const inputMoreShare = d.querySelectorAll('.input-more-share')[0];
    const inputMoreShareValue = inputMoreShare.value;
    if (hasClass(target, 'submit-moreShare') && inputMoreShareValue.length > 0) {
      share.sendMoreShare(inputMoreShareValue);
      inputMoreShare.value = '';
    }
  };

  // DisableExtension
  const disableExtension = (target) => {
    if (hasClass(target, 'disableExtensionItem')) {
      showPopover('disableExtension');
    }
  };

  // Click Interaction
  menu.addEventListener('click', (e) => {
    const target = e.target;
    menuItemInteraction(target);
    triggerColorPicker(target);
    chooseColorInteraction(target);
    saveDraft(target);
    shareTo(target);
    moreShare(target);
    disableExtension(target);
  });

  // Input Interaction
  menu.addEventListener('input', (e) => {
    const target = e.target;
    const className = target.className;
    detectColorPad(target, className);
    rangeInputInteraction(target, className);
    colorPickerInteraction(target);
  });

  // Change Interaction
  menu.addEventListener('change', (e) => {
    const target = e.target;
    const className = target.className;
    fontFamilyInteraction(target, className);
  });
}
