/* eslint max-len: ["error", { "ignoreRegExpLiterals": true }] */

export function hasClass(target, className) {
  return target.className.indexOf(className) > -1;
}

export function addClass(target, className) {
  target.className += ` ${className}`;
  return target;
}

export function removeClass(target, className) {
  const regex = new RegExp(className, 'g');
  target.className = target.className.replace(regex, '').trim();
  return target;
}
export function toggleClass(target, className) {
  const targetClassName = target.className;
  if (hasClass(target, className)) {
    target.className = targetClassName.replace(className, '').trim();
  } else {
    target.className += ` ${className}`;
  }
  return target;
}
export function remove(target) {
  target.parentNode.removeChild(target);
  return target;
}

export function isColor(value) {
  const hexColorRegex = /^#[0-9a-f]{3}([0-9a-f]{3})?$/;
  const rgbColorRegex = /^rgb\\(\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*,\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*,\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*\\)$/;
  const rgbaColorRegex = /^rgba\\(\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*,\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*,\\s*(0|[1-9]\\d?|1\\d\\d?|2[0-4]\\d|25[0-5])\\s*,\\s*((0.[1-9])|[01])\\s*\\)$/;
  const hslColorRegex = /^hsl\\(\\s*(0|[1-9]\\d?|[12]\\d\\d|3[0-5]\\d)\\s*,\\s*((0|[1-9]\\d?|100)%)\\s*,\\s*((0|[1-9]\\d?|100)%)\\s*\\)$/;
  return hexColorRegex.test(value) || rgbColorRegex.test(value) || rgbaColorRegex.test(value) || hslColorRegex.test(value);
}

export function indexInSameClassName(node, className, parentNode) {
  const nodes = parentNode.getElementsByClassName(className);
  let num = 0;
  for (let i = 0; i < nodes.length; i += 1) {
    if (nodes[i] === node) {
      return num;
    }
    num += 1;
  }
  return -1;
}

export function append(dom, html) {
  dom.insertAdjacentHTML('beforeend', html)
}

export function prepend(dom, html) {
  dom.insertAdjacentHTML('afterbegin', html)
}

export function getArrayItemById(array, id) {
  const elementPos = array.map((x) => {
    return x.id;
  }).indexOf(Number(id));
  return array[elementPos];
}