import { hasClass, addClass, removeClass } from './library';

export function popover() {
  const closePopover = (target) => {
    const dom = target.parentNode;
    addClass(removeClass(dom, 'fadeInDown'), 'fadeOutUp');

    const popoverWrapper = dom.parentNode;
    popoverWrapper.style.opacity = 0;
    setTimeout(() => {
      removeClass(dom, 'fadeOutUp');
      removeClass(popoverWrapper, 'active');
    }, 1000);
  };
  const submitFeedback = (target) => {
    const popoverFeedbackTextarea = d.querySelector('.popoverFeedbackTextarea');
    const popoverFeedbackTextareaValue = popoverFeedbackTextarea.value;
    const popoverFeedbackContactValue = d.querySelector('.popoverFeedbackContact').value;
    closePopover(target);
    popoverFeedbackTextarea.value = '';
    ga('send', 'event', 'feedback-fromFeedbackIcon', popoverFeedbackTextareaValue, popoverFeedbackContactValue);
  };
  d.querySelector('.popoverArea').addEventListener('click', (e) => {
    const target = e.target;
    if (hasClass(target, 'submitFeedback')) {
      submitFeedback(target);
    }
    if (hasClass(target, 'closePopover')) {
      ga('send', 'event', 'closePopover', 'close', target.title);
      closePopover(target);
    }
  });
}
export function showPopover(name) {
  const dom = d.querySelector(`.${name}Popover`);
  const popoverWrapper = dom.parentNode;
  addClass(popoverWrapper, 'active');
  popoverWrapper.style.opacity = 1;
  addClass(dom, 'fadeInDown');
}
