import { addClass, hasClass, removeClass, remove } from './library';
import { blurDraft } from './noBehaveProtect';

export default function log() {
  const rateGood = () => {
    ga('send', 'event', 'rate', 'rateGood', Draft.draftCount);
    d.querySelector('.wrapperFeedback').style.opacity = '0';
    const popoverRateGood = '<div class="popoverWrapper active fadeInDown"><div class="popover giveFiveStarPopover"><p class="popoverTip">Please give us a 5 Star Rating!</p><img src="img/slice/5star.png" alt="" class="fiveStar scaleOutIn"></div></div>';
    d.body.insertAdjacentHTML('beforeend', popoverRateGood);
    localStorage.rated = true;
    setTimeout(() => {
      location.href = 'https://chrome.google.com/webstore/detail/new-tab-draft/nmfjkeiebceinkbggliapgfdjphocpdh/reviews';
    }, 1300);
  };
  const rateBad = () => {
    ga('send', 'event', 'rate', 'rateBad', Draft.draftCount);
    localStorage.rated = true;
    d.getElementById('rateBad').style.display = 'none';
    d.getElementById('rateGood').style.display = 'none';
    d.querySelector('.inputFeedbackBad').style.display = 'block';
    d.querySelector('.submitFeedbackBad').style.display = 'block';
  };
  const submitFeedbackBad = () => {
    const inputFeedbackBadValue = d.querySelector('.inputFeedbackBad').value;
    const wrapperFeedback = d.querySelector('.wrapperFeedback');

    ga('send', 'event', 'feedback-fromRateSurvey', inputFeedbackBadValue, appVersion);
    wrapperFeedback.innerHTML = '<p>Thanks for letting us know! We\'ll try and get it fixed as soon as possible.</p>';
    setTimeout(() => {
      wrapperFeedback.style.opacity = 0;
    }, 2000);
    localStorage.rated = true;
  };
  const showNewDesign = () => {
    addClass(menuIconList, 'active shining');
    setTimeout(() => {
      removeClass(menuIconList, 'active shining');
    }, 5000);
  };
  const showLogo = () => {
    addClass(logoDom, 'shining');
    setTimeout(() => {
      removeClass(logoDom, 'shining');
    }, 5000);
  };
  const closeLog = () => {
    const wrapperLogDom = d.querySelector('.wrapperLog');
    ga('send', 'event', 'closeLog', 'click', appVersion);
    addClass(wrapperLogDom, 'fadeOutUpShort');
    setTimeout(() => {
      remove(wrapperLogDom);
    }, 500);
    localStorage.setItem(`log_${appVersion}`, true);
  };
  d.querySelector('.messageArea').addEventListener('click', (e) => {
    const target = e.target;
    if (target.id === 'rateGood') {
      rateGood();
    } else if (target.id === 'rateBad') {
      rateBad();
    } else if (hasClass(target, 'submitFeedbackBad')) {
      submitFeedbackBad();
    } else if (hasClass(target, 'closeLog')) {
      closeLog();
    } else if (hasClass(target, 'logTest-blur')) {
      blurDraft();
      ga('send', 'event', 'logTest', 'blur', appVersion);
    } else if (hasClass(target, 'logTest-FacebookPage')) {
      ga('send', 'event', 'logTest', 'facebookPage', appVersion);
    } else if (hasClass(target, 'logTest-GooglePlus')) {
      ga('send', 'event', 'logTest', 'googlePlus', appVersion);
    } else if (hasClass(target, 'logTest-newDesign')) {
      showNewDesign();
      ga('send', 'event', 'logTest', 'newDesign', appVersion);
    } else if (hasClass(target, 'logTest-logo')) {
      showLogo();
      ga('send', 'event', 'logTest', 'logo', appVersion);
    }
  });
}
