import { removeClass, prepend } from './library';

export default function newNoteItem(createdNotesCount) {
  const noteListDom = d.querySelector('.noteList');
  ga('send', 'event', 'newNoteItem', 'click', appVersion);

  addNewToDom(createdNotesCount);
  addNewToLocalStorage(createdNotesCount);

  function addNewToDom() {
    const activeNoteListItem = d.querySelector('.noteListItem.active');
    if (activeNoteListItem) {
      removeClass(activeNoteListItem, 'active');
    }
    const newNoteItemHTML = `
      <li class="noteListItem active" id=${createdNotesCount}>
        <div class="noteListItem-text"></div>
        <div class="noteListItem-deleteIcon">×</div>
        <div class="noteListItem-deleteSquare">Delete</div>
      </li>`;
    prepend(noteListDom, newNoteItemHTML);
    if (createdNotesCount !== 1) {
      draftDom.value = '';
    }
  }

  function addNewToLocalStorage() {
    const noteListData = JSON.parse(localStorage.noteList);
    const timeStamp = new Date().getTime();
    const newNote = {
      id: createdNotesCount,
      timeStamp,
      text: '',
    };
    noteListData.push(newNote);
    localStorage.noteList = JSON.stringify(noteListData);
    localStorage.createdNotesCount = createdNotesCount;
    draftDom.focus();
  }
}
