import initCustomization from './initCustomization';
import initLog from './initLog';
import initNoteList from './initNoteList';
import { initIconMenuInteraction } from './initIconMenuInteraction';
import logoInteraction from './logoInteraction';
import draftInteraction from './draftInteraction';

export default function init() {
  Draft.noteList = localStorage.getItem('noteList');
  Draft.draft = localStorage.getItem('draft');
  Draft.draftCount = localStorage.getItem('draftCount');
  Draft.rated = localStorage.getItem('rated');
  Draft.log = localStorage.getItem(`log_${appVersion}`);

  // Customisation
  Draft.fontFamily = localStorage.getItem('fontFamily');
  Draft.fontSize = localStorage.getItem('fontSize');
  Draft.fontColor = localStorage.getItem('fontColor');
  Draft.fontColorPad = localStorage.getItem('fontColorPad');
  Draft.backgroundColor = localStorage.getItem('backgroundColor');
  Draft.backgroundColorPad = localStorage.getItem('backgroundColorPad');
  Draft.bookmark = localStorage.getItem('bookmark');

  initCustomization();
  initNoteList();
  initLog();
  logoInteraction();
  draftInteraction();
  initIconMenuInteraction();
}
