export default function getFontFamilyList() {
  chrome.fontSettings.getFontList((fontList) => {
    let optionList = '';
    fontList.map((font) => {
      optionList += `<option name="${font.fontId}">${font.fontId}</option>`;
      return false;
    });
    d.querySelectorAll('.selector-fontFamily')[0].innerHTML = optionList;
  });
}
