import { append, addClass } from './library';

export default function initNoteList() {
  function init() {
    let noteItems = '';
    let noteList = Draft.noteList;
    /*
      [{
        id: createdNotesCount,
        timeStamp,
        text: ' '
      },...]
    */
    if (noteList !== null && noteList !== '[]') {
      noteList = JSON.parse(noteList);
      noteList.sort((a, b) => (b.timeStamp - a.timeStamp));
      for (let i = 0, l = noteList.length; i < l; i += 1) {
        noteItems += `
        <li class="noteListItem" id="${noteList[i].id}">
          <div class="noteListItem-text">${noteList[i].text}</div>
          <div class="noteListItem-deleteIcon">×</div>
          <div class="noteListItem-deleteSquare">Delete</div>
        </li>`;
      }
      append(noteListDom, noteItems);
      draftDom.value = noteList[0].text;
    } else {
      let text = '';
      if (Draft.draft !== null) {
        text = Draft.draft;
      }
      console.log();
      draftDom.value = text;

      const timeStamp = new Date().getTime();
      const newNoteList = [{
        id: 1,
        timeStamp,
        text,
      }];
      localStorage.noteList = JSON.stringify(newNoteList);
      localStorage.createdNotesCount = 1;
      localStorage.noteListMenuStatus = 1;

      const newNoteItemHTML = `
        <li class="noteListItem" id="1">
          <div class="noteListItem-text">${text}</div>
          <div class="noteListItem-deleteIcon">×</div>
          <div class="noteListItem-deleteSquare">Delete</div>
        </li>`;
      append(noteListDom, newNoteItemHTML);
    }

    // Active the first noteListItem
    addClass(d.querySelector('.noteListItem'), 'active');
    if (Number(localStorage.noteListMenuStatus)) {
      addClass(noteListMenuDom, 'active');
      addClass(iconNoteListDom, 'active');
    }
  }

  function setDraftCount() {
    if (Draft.draftCount !== null) {
      let draftCount = Number(Draft.draftCount);
      draftCount += 1;
      localStorage.setItem('draftCount', draftCount);
    } else {
      localStorage.setItem('draftCount', 1);
    }
  }

  init();
  setDraftCount();
}
