import { addClass, removeClass } from './library';

export default function initCustomize() {
  function initFontFamily() {
    if (Draft.fontFamily !== null) {
      setTimeout(() => {
        d.querySelectorAll(`.selector-fontFamily option[name="${Draft.fontFamily}"]`)[0].selected = 'selected';
      }, 100);
    } else {
      setTimeout(() => {
        d.querySelectorAll('.selector-fontFamily option[name="Arial"]')[0].selected = 'selected';
      }, 100);
    }
  }
  function initFontSize() {
    if (Draft.fontSize !== null) {
      d.querySelector('.fontSize-number').innerText = Draft.fontSize;
      d.querySelector('.fontSize-rangeChanger').value = Draft.fontSize;
    }
  }
  function colorPadInition(colorPad) {
    if (Draft[`${colorPad}Pad`] !== null) {
      const colorPadDom = d.querySelector(`.${colorPad}`);
      const colorPadItemDoms = colorPadDom.querySelectorAll('.colorPadItem');
      const colorPadItemLength = colorPadItemDoms.length;

      const colorPadNumber = Draft[`${colorPad}Pad`];
      removeClass(colorPadDom.querySelector('.colorPadItem.active'), 'active');
      addClass(colorPadItemDoms[colorPadNumber], 'active');
      const activeColorPad = colorPadItemDoms[colorPadNumber];
      // Detect if user made a color, fill in the color
      if (Number(Draft[`${colorPad}Pad`]) === colorPadItemLength - 1) {
        activeColorPad.style.backgroundColor = Draft[colorPad];
        activeColorPad.nextElementSibling.value = Draft[colorPad];
      }
    }
  }
  function initFontColor() {
    colorPadInition('fontColor');
  }
  function initBackgroundColor() {
    colorPadInition('backgroundColor');
  }

  initFontFamily();
  initFontSize();
  initFontColor();
  initBackgroundColor();
}
