import { showPopover } from './popover';
import { hasClass, toggleClass, addClass, removeClass } from './library';

export function initIconMenuInteraction() {
  menuIconList.addEventListener('click', (e) => {
    const target = e.target;
    if (hasClass(target, 'icon-noteList')) {
      const noteListMenuDom = d.querySelector('.noteListMenu');
      toggleClass(noteListMenuDom, 'active');
      toggleClass(iconNoteListDom, 'active');
      if (hasClass(noteListMenuDom, 'active')) {
        localStorage.noteListMenuStatus = 1;
      } else {
        localStorage.noteListMenuStatus = 0;
      }
      ga('send', 'event', 'iconNoteList', 'click', appVersion);
    } else if (hasClass(target, 'icon-settings')) {
      if (hasClass(menu, 'active')) {
        hideSettingsMenu();
      } else {
        addClass(menu, 'active');
        addClass(iconSettingsDom, 'active');
      }
      ga('send', 'event', 'iconSettings', 'click', appVersion);
    } else if (hasClass(target, 'feedback')) {
      showPopover('feedback');
      ga('send', 'event', 'iconFeedback', 'click', appVersion);
    }
  });
}

export function hideSettingsMenu() {
  const menu = d.querySelector('.menu');
  if (hasClass(menu, 'active')) {
    const activeMenuItem = document.querySelector('.menuItem-list-item.groupItem.active');
    removeClass(menu, 'active');
    removeClass(iconSettingsDom, 'active');
    if (activeMenuItem) {
      removeClass(activeMenuItem, 'active');
    }
  }
}
