import { addClass, removeClass } from './library';

export default function toast(type, text) {
  if (type === 'success') {
    toastDom.innerText = text;
    addClass(removeClass(toastDom, 'moveUp'), 'moveDown');
    setTimeout(() => {
      addClass(removeClass(toastDom, 'moveDown'), 'moveUp');
    }, 3000);
  }
}
