export default function initCustomization() {
  function initFontFamily() {
    if (Draft.fontFamily !== null) {
      draftDom.style.fontFamily = Draft.fontFamily;
    }
  }
  function initFontSize() {
    if (Draft.fontSize !== null) {
      draftDom.style.fontSize = `${Draft.fontSize}px`;
    }
  }
  function initFontColor() {
    if (Draft.fontColor !== null) {
      draftDom.style.color = Draft.fontColor;
      noteListMenuDom.style.color = Draft.fontColor;
      wrapperLogDom.style.color = Draft.fontColor;
      wrapperFeedbackDom.style.color = Draft.fontColor;
      menu.style.color = Draft.fontColor;
    }
  }
  function initBackgroundColor() {
    if (Draft.backgroundColor !== null) {
      d.body.style.backgroundColor = Draft.backgroundColor;
      noteListMenuDom.style.backgroundColor = Draft.backgroundColor;
      wrapperLogDom.style.backgroundColor = Draft.backgroundColor;
      wrapperFeedbackDom.style.backgroundColor = Draft.backgroundColor;
      menu.style.backgroundColor = Draft.backgroundColor;
    }
  }
  initFontFamily();
  initFontSize();
  initFontColor();
  initBackgroundColor();
}
