export function toTwitter() {
  const shareLink = d.createElement('a');
  shareLink.href = `https://twitter.com/intent/tweet?text=${encodeURIComponent(draftDom.value)}`;
  shareLink.target = '_blank';
  shareLink.click();
}
export function sendMoreShare(value) {
  ga('send', 'event', 'moreShare', 'click', value);
  toast('success', 'Draft team has received your feedback :D');
}
