import { hasClass, addClass, removeClass } from './library';

let noBehaveProtector;
export function noBehaveProtect() {
  const setNoBehaveProtect = () => {
    noBehaveProtector = setInterval(blurDraft, 1000 * 60 * 10);
  };
  const resetNoBehaveProtect = () => {
    clearInterval(noBehaveProtector);
    setNoBehaveProtect();
    if (hasClass(draftDom, 'blurIn')) {
      addClass(removeClass(draftDom, 'blurIn'), 'blurOut');
    }
  };
  d.addEventListener('mousemove', () => {
    resetNoBehaveProtect();
  });
  d.addEventListener('keydown', () => {
    resetNoBehaveProtect();
  });
  setNoBehaveProtect();
}
export function blurDraft() {
  addClass(removeClass(draftDom, 'blurOut'), 'blurIn');
  clearInterval(noBehaveProtector);
}
