/* eslint no-param-reassign: ["error", { "props": false }] */

export function fontFamily(value) {
  draftDom.style.fontFamily = value;
}
export function fontSize(value) {
  draftDom.style.fontSize = `${value}px`;
  d.querySelector('.fontSize-number').innerText = value;
}
export function fontColor(target, value, colorPicker) {
  draftDom.style.color = value;
  noteListMenuDom.style.color = value;
  wrapperLogDom.style.color = value;
  wrapperFeedbackDom.style.color = value;
  menu.style.color = value;
  if (colorPicker === 'colorPicker') {
    target.previousElementSibling.value = value;
    target.previousElementSibling.previousElementSibling.style.backgroundColor = value;
  }
}
export function backgroundColor(target, value, colorPicker) {
  d.body.style.backgroundColor = value;
  noteListMenuDom.style.backgroundColor = value;
  wrapperLogDom.style.backgroundColor = value;
  wrapperFeedbackDom.style.backgroundColor = value;
  menu.style.backgroundColor = value;
  if (colorPicker === 'colorPicker') {
    target.previousElementSibling.value = value;
    target.previousElementSibling.previousElementSibling.style.backgroundColor = value;
  }
}
