export default function calculate(e, value) {
  if (calculatable) {
    if (e.which === 13) {
      if (value.match(/\n/g).length < 20) {
        const arrayValue = value.split('\n');
        const arrayLength = arrayValue.length;
        try {
          if (!isNaN(eval(arrayValue[arrayLength - 2]))) {
            if (arrayValue[arrayLength - 2].indexOf('+') > -1 || arrayValue[arrayLength - 2].indexOf('-') > -1 || arrayValue[arrayLength - 2].indexOf('*') > -1 || arrayValue[arrayLength - 2].indexOf('/') > -1) {
              draftDom.value = `${draftDom.value.slice(0, -1)}=${eval(arrayValue[arrayLength - 2])}\n`;
              ga('send', 'event', 'calculate', 'enter', draftDom.value);
            }
          }
        } catch (err) {
          ga('send', 'event', 'error', 'calculateError', err);
        }
      }
    }
  }
}
