export default function saveFile() {
  const textToSave = draftDom.value;
  const textFileAsBlob = new Blob([textToSave], { type: 'text/plain' });
  const date = new Date();
  const fileDate = `${date.toLocaleTimeString()} ${date.toLocaleDateString()}`;
  const fileNameToSaveAs = `Draft at ${fileDate}`;

  const downloadLink = d.createElement('a');
  downloadLink.download = fileNameToSaveAs;
  downloadLink.innerHTML = 'Download File';
  if (window.URL != null) {
    // Chrome allows the link to be clicked
    // without actually adding it to the DOM.
    downloadLink.href = window.URL.createObjectURL(textFileAsBlob);
  }
  downloadLink.click();
}
