import calculate from './calculate';
import { hideSettingsMenu } from './initIconMenuInteraction';
import newNoteItem from './newNoteItem';

export default function draftInteraction() {
  draftDom.onkeyup = (e) => {
    const value = e.target.value;
    const activeNoteListItem = d.querySelector('.noteListItem.active');
    if (activeNoteListItem) {
      const id = activeNoteListItem.id;
      saveDraft(id, value);
      calculate(e, value, this);
    } else {
      const createdNotesCount = 1;
      newNoteItem(createdNotesCount);
    }
    // Todo: 判断 title 是否有变化。如果没有变化就不重写
    d.querySelector('.noteListItem.active .noteListItem-text').innerText = value.split('\n')[0];
  };
  draftDom.onclick = () => {
    hideSettingsMenu();
  };
  function saveDraft(id, value) {
    const noteListData = JSON.parse(localStorage.noteList);
    for (let i = noteListData.length - 1; i >= 0; i -= 1) {
      if (noteListData[i].id === Number(id)) {
        noteListData[i].text = value;
        noteListData[i].timeStamp = new Date().getTime();
        break;
      }
    }
    localStorage.noteList = JSON.stringify(noteListData);
  }
}
